import { Directive, ElementRef, HostListener, Input, Renderer } from '@angular/core';

@Directive({ 
    selector: '[zoom]'
})

export class ZoomDirective {
    constructor(private el: ElementRef, private renderer: Renderer) {  
        
    }
    private element2 : number = 0;
    private element : boolean = false;

    @HostListener('mouseleave') onMouseLeave() {
        this.border();
    }
    @HostListener('wheel', ['$event']) onWheel(event: any) {
        event.preventDefault();
        if(this.element == true){
            if(event.deltaY > 0){
                this.changeSize(-25);
            }        
            if(event.deltaY < 0){
                this.changeSize(25);
            }
        }
    }
    @HostListener('click') onClick(){
      if(this.element == false){
          this.changeSize(200);
          this.element = true;
      }else{
            this.backToTheSize();
            this.element = false;
      }
    }
    private border(
      color: string = null, 
      type: string = null, 
      width: string = null
      ){
        this.renderer.setElementStyle(
            this.el.nativeElement, 'border-color', color);
        this.renderer.setElementStyle(
            this.el.nativeElement, 'border-style', type);
        this.renderer.setElementStyle(
            this.el.nativeElement, 'border-width', width);
    }
    private changeSize(sizechange: any){
        let height: any = this.el.nativeElement.offsetHeight;
        let newHeight: any = height + sizechange;
        if( 100<= newHeight && newHeight < 900){
        this.renderer.setElementStyle(
            this.el.nativeElement, 'height', newHeight + 'px');
        }
    }
    private backToTheSize(){
        this.renderer.setElementStyle(
            this.el.nativeElement, 'height', 300 + 'px');
    }

}